/* src/LandingPage.css */
.landing-page {
    display: flex;
    justify-content: center; /* Centers the content horizontally */
    align-items: center; /* Centers the content vertically */
    height: 100vh; /* Full viewport height */
    padding: 0 20px; /* Add some padding to prevent content from touching the sides */
  }
  
  .left-side, .right-side {
    padding: 20px; /* Add some padding inside each side */
  }
  
  .left-side {
    text-align: left;
  }
  
  .right-side {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .right-side img {
    max-width: 100%;
    height: auto;
    border-radius: 20px; /* Optional: Add rounded corners to the image */
  }
  